<script>
export default {
    props:{
        value:{
            type:[String,Number],
            default:''
        }
    },
    render(h){
        var _this =this;
        return h('div',[
            h('span','昵称： '),
            h('input',{
                attrs:{
                    type:'text'
                },
                domProps:{
                    value:this.vlaue
                },
                on:{
                    input(event){
                        //_this.value = event.target.vlaue;
                        _this.$emit('input',event.target.value);
                    }
                }
            })
        ]);
    }
}
</script>